:root {
  --dark-bg: #0a192f;
  --dark-text: #ccd6f6;
  --light-bg: #ffffff;
  --light-text: #0a192f;
  --primary: #64ffda;
  --transition: 0.3s ease;
}

.skills-container {
  background: var(--dark-bg);
  color: var(--dark-text);
  padding: 40px 20px;
  font-family: "Segoe UI", Roboto, sans-serif;
}

.skills-title {
  font-size: 2rem;
  text-align: center;
  color: var(--primary);
  margin-bottom: 30px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

/* Categories Carousel */
.categories-carousel {
  display: flex;
  gap: 20px;
  overflow: hidden;
  padding: 40px 20px;
  scroll-behavior: smooth;
  height: 23vh;
}

.category-card {
  flex: 0 0 auto;
  width: 150px;
  height: 150px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  text-align: center;
}

.category-card:hover {
  transform: scale(1.1);
  box-shadow: 0 12px 24px rgba(0, 212, 255, 0.6);
}

.category-icon {
  font-size: 2.5rem;
  margin-bottom: 10px;
  color: var(--primary);
}

.category-name {
  font-size: 1rem;
  font-weight: 500;
}

/* Expanded Skills Section */
.skills-expanded {
  margin-top: 20px;
}

.expanded-header {
  display: flex;
  align-items: center;
  gap: 15px;
  margin-bottom: 20px;
}

.expanded-header h3 {
  font-size: 1.5rem;
  color: var(--primary);
}

.back-btn {
  background: var(--dark-bg);
  border: none;
  cursor: pointer;
  color: var(--primary);
  font-size: 1.5rem;
  transition: transform 0.3s ease;
}

.back-btn:hover {
  transform: scale(1.2);
}

/* Skills Grid Horizontal Scroll */
.skills-grid {
  display: flex;
  gap: 20px;
  overflow: hidden;
  padding: 20px 0;
}

/* Individual Skill Card */
.skill-card {
  flex: 0 0 auto;
  width: 140px;
  height: 140px;
  background: rgba(255,255,255,0.05);
  border-radius: 15px;
  text-align: center;
  padding: 15px;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0,0,0,0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  position: relative; /* needed for hover glow */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Icon inside skill card */
.skill-card svg {
  width: 60px;
  height: 60px;
  margin-bottom: 10px;
  transition: transform 0.3s ease;
  color: var(--primary);
}

.skill-card:hover svg {
  transform: scale(1.1);
}

.skill-name {
  font-size: 0.9rem;
  color: var(--dark-text);
  word-break: break-word;
}

/* Glow effect for cards */
.skill-card:hover::after {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(0, 212, 255, 0.3) 0%, transparent 70%);
  transform: rotate(25deg);
  opacity: 0.6;
  pointer-events: none;
  animation: shimmer 2s infinite linear;
  border-radius: inherit;
}

@keyframes shimmer {
  0% { transform: translateX(-200%) ; }
  100% { transform: translateX(200%) ; }
}

/* Responsive adjustments */
@media (max-width: 1200px) {
  .skills-container { max-width: 1000px; }
}

@media (max-width: 1024px) {
  .category-card { width: 120px; height: 120px; }
  .skills-grid { gap: 15px; }
  .skill-card { width: 120px; height: 120px; }
}

@media (max-width: 768px) {
  .skills-container { max-width: 90%; padding: 30px 10px; }
  .skills-title { font-size: 1.8rem; }
  .expanded-header h3 { font-size: 1.2rem; }
  .category-card { width: 120px; height: 120px; }
  .skills-grid { gap: 10px; }
  .skill-card { width: 120px; height: 120px; }
}
